    import QRCode from 'qrcodejs2';
    export default {
        data() {
            return {
                upobj: {
                    height: 20,
                    upContent: '松开刷新数据',
                    downContent: '下拉刷新数据',
                    loadingContent: '下拉刷新数据',
                    clsPrefix: 'xs-plugin-pullup-'
                },
                showUp: true,    //  允许下拉刷新插件
                come: null, //  判断是哪里跳转
                headValue: 0,    // 头部(呼叫服务/异常列表)切换值
                callValue: 0,    // 呼叫服务=>(呼叫中/已解决)切换值
                abnormal: 0,      // 异常列表=>(投诉/已解决)切换值
                allNum: 0,    //  判断呼叫中=>是点击的哪个选项中的详情按钮
                allNumOver: 0,    //  判断已解决=>是点击的哪个选项中的详情按钮
                defaultValue: null, //  判断是否接单
                user_id: null,
//                呼叫服务=>呼叫中列表
                callList: [],
//                呼叫服务=>已解决列表
                solveList: [],
//                异常列表=>投诉列表
                complainList: [],
//                异常列表=>已解决列表
                overList: [],
                yawpNum: '!',    //  投诉条数
                shows: false,    // 呼叫列表=>呼叫中选项=>点击误操作弹窗
                overShows: false,    // 呼叫列表=>呼叫中选项=>点击已处理弹窗
                all: false,    // 呼叫列表=>呼叫中选项=>详情
                allOver: false,    // 呼叫列表=>已解决选项=>详情
                show: false,    // 异常列表=>投诉选项=>点击误操作值
                overIndex: 0,       //异常列表=>投诉选项=>判断点击的是第几项
                overnum: 0,       //呼叫列表=>呼叫中选项=>判断点击的是第几项
                openDetails: false, //查看详情
                limit: 10,     //  一次限制请求几条数据
                onFetching: false,
                dizhi: 'https://demo.isaveu.cn/mobile/#/userEvaluation',

                allCount: null,     //  呼叫列表中=>呼叫条数
                page: 1,            //  呼叫列表请求页码页数
                maxPage: 1,
                loadText: '正在拼命加载中',        //  呼叫中列表  上拉提示词
                loading: true,                  //   呼叫中列表   上拉圈圈
                id: null,               //   呼叫中准备操作的数据ID
                shop_id: null,               //   呼叫中准备操作的数据shop_id
                PositionValue: null,    //  当操作失败的时候  返回的提示信息
                showPosition: false,   //  控制操作失败时弹窗显示或隐藏

                pageSolve: 1,                       //  呼叫服务=>已解决列表请求页码页数
                maxPageSolve: 1,
                loadTextOver: '正在拼命加载中',    //  呼叫服务=>已解决列表 上拉提示词
                loadingOver: true,                  //   呼叫服务=>已解决列表   上拉圈圈

                idYawp: null,                           //   异常列表=>投诉列表准备操作的数据ID
                pageYawp: 1,                       //  异常列表=>投诉列表请求页码页数
                maxPageYawp: 1,
                loadTextYawp: '正在拼命加载中',    //  投诉列表  上拉提示词
                loadingYawp: true,                  //   投诉列表   上拉圈圈

                pageOverList: 1,                       //  异常列表=>已解决列表请求页码页数
                maxPageOverList: 1,
                loadTextOverList: '正在拼命加载中',    //  异常列表=>已解决列表  上拉提示词
                loadingOverList: true,                  //   异常列表=>已解决列表   上拉圈圈
            }
        },
        created(){
            this.come = this.$route.params.come || null;
            this.headValue = this.$route.params.headValue || 0;
            this.callValue = this.$route.params.callValue || 0;
            this.abnormal = this.$route.params.abnormal || 0;
            if(this.headValue == 1 && this.abnormal == 0){
                this.complainList = [];
                this.pageYawp = 1;
                this.maxPageYawp = 1;
                this.yawoDown();
            }
            if(this.headValue == 1 && this.abnormal == 1){
                this.overList = [];
                this.pageOverList = 1;
                this.maxPageOverList = 1;
                this.overDown();
                setTimeout(()=>{
                    this.yawoDown();
                },100)
            }
        },
        mounted() {
            if(this.come != null){
                this.$router.go(0)
            };
            this.user_id = window.localStorage.getItem("user_id");
            this.getInfo(); //初始化数据
            if( this.callList.length < 6 ){
                this.loadText = '已经到底了哦';
                this.loading = false;
            };
        },
        methods:{
            // 呼叫服务=>呼叫中=>下拉刷新
            callPullUp(){
                this.page = 1;
                this.callList = [];
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.callList,
                    type: "post",
                    data: {
                        page: this.page,
                        limit: this.limit,
                    },
                    success: (response, allResponse) => {
                        this.allCount = allResponse.count;
                        this.callList = this.callList.concat(response);
                        this.$refs.scrollerBottom.reset({top: 0});
                        this.page ++;
                    }
                });
            },
            // 呼叫服务=>已解决=>下拉刷新
            slovePullUp(){
                this.pageSolve = 1;
                this.solveList = [];
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.callSolve,
                    type: "post",
                    data: {
                        page: this.pageSolve,
                        limit: this.limit,
                    },
                    success: (response, allResponse) => {
                        this.solveList = this.solveList.concat(response);
                        this.$refs.scrollerBottom.reset({top: 0});
                        this.pageSolve ++;
                    }
                });
            },
            // 异常呼叫=>投诉列表=>下拉刷新
            yawpPullUp(){
                this.pageYawp = 1;
                this.complainList = [];
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.abnormalList,
                    type: "post",
                    data: {
                        page: this.pageYawp,
                        limit: this.limit,
                    },
                    success: (response, allResponse) => {
                        this.complainList = this.complainList.concat(response);
                        this.$refs.scrollerBottom.reset({top: 0});
                        this.pageYawp ++;
                    }
                });
            },
            // 异常呼叫=>已解决=>下拉刷新
            overPullUp(){
                this.pageOverList = 1;
                this.overList = [];
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.abnormalSolve,
                    type: "post",
                    data: {
                        page: this.pageOverList,
                        limit: this.limit,
                    },
                    success: (response, allResponse) => {
                        this.overList = this.overList.concat(response);
                        this.$refs.scrollerBottom.reset({top: 0});
                        this.pageOverList ++;
                    }
                });
            },
            // 点击扫码弹窗右上角的X按钮
            clearOver(){
                this.openDetails = false;
                this.$router.go(0)
            },
            // 生成二维码
            qrcode(){
                let qrcode = new QRCode('qrcode', {
                    width: 120,
                    height:120,
                    text: this.dizhi,
                })
                console.log(qrcode._htOption.text)
            },
            // 服务员点击接单
            accept(item,index){
                this.id = item.id;
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.admit,
                    type: "post",
                    data: {
                        id: this.id
                    },
                    success: (response, allResponse) => {
                        if(allResponse.code == 0){
                            this.defaultValue = index;
                        }
                        if(allResponse.code == 1){
                            this.defaultValue = null;
                        }
                    }
                });
            },
            // 呼叫界面上拉加载更多
            getInfo() {
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.callList,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                        },
                        success: (response, allResponse) => {
                            // console.log('response',response)
                            this.allCount = allResponse.count;
                            this.onFetching = true;
                            this.maxPage = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.callList = this.callList.concat(response);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
//            头部(呼叫服务/异常列表)切换
            consoleIndex(){
                // console.log('头部(呼叫/异常)切换值:',this.headValue);
                if( this.headValue == 0 ){
                    this.abnormal = 0;
                    this.page = 1;
                    this.maxPage = 1;
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.callList = [];
                    this.getInfo();
                }
                if( this.headValue == 1 ){
                    this.complainList = [];
                    this.pageYawp = 1;
                    this.maxPageYawp = 1;
                    this.callValue = 0;
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.yawoDown();
                }
            },
            // 异常列表=>投诉列表上拉加载更多
            yawoDown(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.pageYawp > this.maxPageYawp){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.abnormalList,
                        type: "post",
                        data: {
                            page: this.pageYawp,
                            limit: this.limit,
                        },
                        success: (response, allResponse) => {
                            // console.log(response)
                            this.yawpNum = allResponse.count;
                            this.onFetching = true;
                            this.complainList = this.complainList.concat(response);
                            this.maxPageYawp = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.pageYawp >= this.maxPageYawp){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadTextYawp = "已经到底了哦~";
                                this.loadingYawp = false;
                            }
                            this.onFetching = false;
                            this.pageYawp ++;
                        }
                    });
                }
            },
            // 异常列表=>已解决列表上拉加载更多
            overDown(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.pageOverList > this.maxPageOverList){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.abnormalSolve,
                        type: "post",
                        data: {
                            page: this.pageOverList,
                            limit: this.limit,
                        },
                        success: (response, allResponse) => {
                            // console.log(response)
                            this.onFetching = true;
                            this.overList = this.overList.concat(response);
                            this.maxPageOverList = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.pageOverList >= this.maxPageOverList){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadTextOverList = "已经到底了哦~";
                                this.loadingOverList = false;
                            }
                            this.onFetching = false;
                            this.pageOverList ++;
                        }
                    });
                }
            },
//            呼叫服务=>呼叫中
            onItemClick () {
                // console.log('呼叫模块(呼叫中/已解决)切换值:', index);
                this.defaultValue = null;
                this.pageSolve = 1;
                this.callValue = 0;
                this.$refs.scrollerBottom.reset({top: 0});
                this.callList = [];
                this.solveList = [];
                this.page = 1;
                this.getInfo();
            },
            // 呼叫服务=>点击接收订单
            // consent(index){
            //     this.defaultValue = index
            // },
            // 点击呼叫服务中=>已解决
            solveOver(){
                this.page = 1;
                this.callValue = 1;
                this.$refs.scrollerBottom.reset({top: 0});
                this.solveList = [];
                this.pageSolve = 1;
                this.callSolve();
            },
            // 呼叫服务=>已解决界面上拉加载更多
            callSolve(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.pageSolve > this.maxPageSolve){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.callSolve,
                        type: "post",
                        data: {
                            page: this.pageSolve,
                            limit: this.limit,
                        },
                        success: (response, allResponse) => {
                            // console.log(response)
                            this.onFetching = true;
                            this.solveList = this.solveList.concat(response);
                            this.maxPageSolve = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.pageSolve >= this.maxPageSolve){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadTextOver = "已经到底了哦~";
                                this.loadingOver = false;
                            }
                            this.onFetching = false;
                            this.pageSolve ++;
                        }
                    });
                }
            },
            // 呼叫服务=>呼叫中详情
            allDetail(item){
                this.all = !this.all;
                this.allNum = item.data
            },
            // 呼叫服务=>已解决详情
            overSolve(item){
                this.allOver = !this.allOver;
                this.allNumOver = item.desc
            },
//            服务列表=>呼叫中界面=>已处理按钮
            already(index,item){
                console.log("我是已处理=>",index);
                this.overShows = true;
                this.id = item.id;
                this.shop_id = item.shop_id;
            },
            // 服务列表=>呼叫中界面=>已处理按钮=>确定按钮
            finish(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.callDeal,
                    type: "post",
                    data: {
                        id: this.id,
                        status: 1,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',allResponse)
                        if( allResponse.code == 1 ){
                            this.PositionValue = allResponse.msg;
                            this.showPosition = true;
                        }
                        if( allResponse.code == 0 ){
                            this.defaultValue = null;
                            this.dizhi = this.dizhi + '?shopId=' + this.shop_id + '&id=' + this.id + '&waiterId=' + this.user_id;
                            this.qrcode();
                            this.openDetails = !this.openDetails;  //  显示扫码页面弹窗
                            this.callSolve();
                            this.$refs.scrollerBottom.reset({top: 0});
                            this.page = 1;
                            this.callValue = 1;
                            this.overShows = false;   // 关闭弹窗
                            this.solveList.splice(0,0,this.callList[this.overnum]);   // 往已解决数组中添加点击误操作的项数据
                        }
                    }
                });
            },
//            服务列表=>呼叫中界面=>误操作按钮
            fault(index,item){
                this.id = item.id;
                this.shows = true;
                this.overnum = index;
            },
//            服务列表=>呼叫中界面=>误操作按钮=>确定按钮
            onConfirms(){
                this.defaultValuev = null;
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.callDeal,
                    type: "post",
                    data: {
                        id: this.id,
                        status: 2,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',allResponse)
                        if( allResponse.code == 1 ){
                            this.PositionValue = allResponse.msg;
                            this.showPosition = true;
                        }
                        if( allResponse.code == 0 ){
                            this.$refs.scrollerBottom.reset({top: 0});
                            this.page = 1;
                            this.callValue = 1;
                            this.shows = false;   // 关闭弹窗
                            // this.solveList.splice(0,0,this.callList[this.overnum]);   // 往已解决数组中添加点击误操作的项数据
                            this.callSolve();
                        }
                    }
                });
            },
//            异常列表(呼叫中/已解决)切换
            onItemClicks(index){
                // console.log('异常列表(投诉/已解决)切换值:', index);
                this.abnormal = index;
                if( index == 0 ){
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.complainList = [];
                    this.pageOverList = 1;
                    this.pageYawp = 1;
                    this.maxPageYawp = 1;
                    this.yawoDown();
                }
                if( index == 1 ){
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.overList = [];
                    this.pageYawp = 1;
                    this.pageOverList = 1;
                    this.maxPageOverList = 1;
                    this.overDown();
                }
            },
//             异常列表=>投诉界面=>写原由按钮
            noAlready(index,item){
                this.$router.push({
                    path: '/writeReason',
                    query: {
                        id: item.id
                    }
                })
            },
//            异常列表=>投诉界面=>误操作按钮
            noFault(index,item){
                this.show = true;
                this.overIndex = index;
                this.idYawp = item.id
            },
//            异常列表=>投诉界面=>误操作按钮=>确定按钮
            onConfirm(){
                console.log('我误操作了=>我点击的是第',this.overIndex)
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.abnormalDeal,
                    type: "post",
                    data: {
                        id: this.idYawp,
                        status: 2,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',allResponse)
                        if( allResponse.code == 1 ){
                            this.PositionValue = allResponse.msg;
                            this.showPosition = true;
                        }
                        if( allResponse.code == 0 ){
                            this.overDown();
                            this.$refs.scrollerBottom.reset({top: 0});
                            this.pageOverList = 1;
                            this.abnormal = 1;
                            this.show = false;   // 关闭弹窗
                            this.overList.splice(0,0,this.complainList[this.overIndex]);    // 往已解决数组中添加点击误操作的项数据
                            this.complainList.splice(this.overIndex,1);    // 删除在投诉选项中点击误操作的项数据
                        }
                    }
                });
            },
//            异常列表=>已解决界面=>查原由按钮
            origin(index,item){
                // console.log("我要查原由的ID是,",item.id)
                this.$router.push({
                    path: 'lookReason',
                    query: {
                        id: item.id
                    }
                })
            },
        },
        // 路由守卫,监听上一次URL地址
        // beforeRouteEnter (to, from, next) {
        //     if(from.path == '/login'){
        //         location.reload();
        //     }
        //     next();
        // },
    }



